@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
}

body {
  background-color: #f5f6fa;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding: 20px;
}

.app__header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
}
.app__header h1 {
  color: rgb(85, 85, 85);
}

.app__stats {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.app__left {
  -webkit-flex: 0.9 1;
          flex: 0.9 1;
}

.app__right {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.app__graphTitle {
  margin-top: 20px;
  margin-bottom: 20px;
}

.app__right .MuiCardContent-root {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.app__graph {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
.app:last-child {
  display: block;
}
.footer {
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 100px;
  border-radius: 20px;
  text-align: center;
}
.footer h3 {
  padding: 20px;
  font-size: 2vh;
  font-family: "Trebuchet MS", sans-serif;
  font-weight: 100;
  color: #808080;
}
.footer h4 {
  font-size: 2vh;
  font-family: "Trebuchet MS", sans-serif;
  font-weight: 100;
  color: #808080;
}
@media (max-width: 990px) {
  .app {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}
@media (max-width: 450px) {
  .app__stats {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}

.infoBox {
  -webkit-flex: 1 1;
          flex: 1 1;
  cursor: pointer;
  margin: 3px;
}

.infoBox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}
.infoBox:hover {
  background-color: rgb(248, 248, 248);
}
.infoBox--selected {
  border-top: 10px solid greenyellow;
}

.infoBox--red {
  border-color: red;
}

.infoBox__cases--green {
  color: lightgreen;
}

.infoBox--grey {
  border-color: grey;
}

.infoBox__cases--grey {
  color: grey;
}

.infoBox__total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}
/* @media (max-width: 450px) {
  .infobox {
    flex-direction: column;
  }
} */

.map {
  height: 500px;
  background-color: white;
  padding: 1rem;
  border-radius: 20px;
  margin-top: 16px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.info-flag img {
  width: 100px;
  border-radius: 5px;
}

.info-flag {
  height: 80px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
}

.info-confirmed,
.info-deaths,
.info-recovered {
  font-size: 16px;
  margin-top: 5px;
}

.info-name {
  font-size: 20px;
  font-weight: bold;
  color: #555;
}

.table {
  margin-top: 20px;
  overflow: scroll;
  height: 400px;
  color: #6a5d5d;
  background-color: #fff;
}

.table tr {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.table td {
  padding: 0.5rem;
}
.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}

